// 配置路由的地方
import Vue from "vue";
import VueRouter from "vue-router";

// 使用插件
Vue.use(VueRouter)

// 引入路由组件
import Convert from '@/pages/Convert'

// 配置路由，需要先暴露一个vue-router类的实例
export default new VueRouter({
    // 配置路由
    routes: [
        {
            path: "/Convert",
            component: Convert,
            meta: { show: true }
        },
        // {
        //     path: "/login",
        //     component: Login,
        //     meta: { show: false }
        // },
        // {
        //     path: "/register",
        //     component: Register,
        //     meta: { show: false }
        // },
        // {
        //     path: "/search/:keyword",
        //     component: Search,
        //     meta: { show: true },
        //     name: 'search'
        // },
        {
            // 重定向，在项目跑起来的时候，访问/，立马重定向到首页
            path: "*",
            redirect: 'Convert'
        },
    ]
})
